/**
 * Main script for converting Pikachu Volleyball replay files to video
 * Real-time recording with MediaRecorder at 30 FPS
 * FIXES: Hash Mismatch, Audio Capture, Canvas Overlay (Chat/Nicknames), Robust Error Handling
 */
'use strict';
import { settings } from '@pixi/settings';
import { SCALE_MODES } from '@pixi/constants';
import { Renderer, BatchRenderer, autoDetectRenderer } from '@pixi/core';
import { Prepare } from '@pixi/prepare';
import { Container } from '@pixi/display';
import { Loader } from '@pixi/loaders';
import { SpritesheetLoader } from '@pixi/spritesheet';
import { Ticker } from '@pixi/ticker';
import { CanvasRenderer } from '@pixi/canvas-renderer';
import { CanvasSpriteRenderer } from '@pixi/canvas-sprite';
import { CanvasPrepare } from '@pixi/canvas-prepare';
import '@pixi/canvas-display';
import { ASSETS_PATH } from '../offline_version_js/assets_path.js';
import { PikachuVolleyballReplay } from './pikavolley_replay.js';
import { setGetSpeechBubbleNeeded } from '../chat_display.js';
import { serialize } from '../utils/serialize.js';
import { getHashCode } from '../utils/hash_code.js';
import '../../style.css';

console.log('🎬 [INIT] main_out.js loaded');

// [1] 전역 DOM 에러 방지 (Global Monkey Patch)
// chat_display.js가 DOM을 조작할 때 발생하는 NotFoundError를 원천 차단합니다.
const originalReplaceChild = Node.prototype.replaceChild;
Node.prototype.replaceChild = function(newChild, oldChild) {
    try {
        return originalReplaceChild.call(this, newChild, oldChild);
    } catch (e) {
        // 에러 발생 시, 교체 대신 그냥 추가(append)하여 내용이라도 표시
        try { this.appendChild(newChild); } catch (e2) {}
        return newChild;
    }
};

const originalRemoveChild = Node.prototype.removeChild;
Node.prototype.removeChild = function(child) {
    try {
        return originalRemoveChild.call(this, child);
    } catch (e) {
        return null; // 없는 요소 삭제 시도 시 조용히 무시
    }
};

class ReplayToVideoConverter {
  constructor() {
    console.log('🎬 [CONSTRUCTOR] Creating ReplayToVideoConverter');
    
    Renderer.registerPlugin('prepare', Prepare);
    Renderer.registerPlugin('batch', BatchRenderer);
    CanvasRenderer.registerPlugin('prepare', CanvasPrepare);
    CanvasRenderer.registerPlugin('sprite', CanvasSpriteRenderer);
    Loader.registerPlugin(SpritesheetLoader);
    settings.RESOLUTION = 2;
    settings.SCALE_MODE = SCALE_MODES.NEAREST;
    settings.ROUND_PIXELS = true;

    this.renderer = null;
    this.stage = null;
    this.loader = null;
    this.ticker = null;
    this.pikaVolley = null;
    this.currentPack = null;
    
    this.mediaRecorder = null;
    this.recordedChunks = [];
    this.isRecording = false;
    
    // 오디오 캡처용
    this.audioDest = null;
    this.audioCtx = null;
  }

  updateStatus(message) {
    const statusEl = document.getElementById('status');
    if (statusEl) statusEl.textContent = message;
    console.log(`📝 [STATUS] ${message}`);
  }

  handleRecordButtonClick() {
    if (this.isRecording) return;

    // [중요] 브라우저 정책상 사용자 클릭 시 AudioContext를 활성화해야 함
    // @ts-ignore
    const AudioContext = window.AudioContext || window.webkitAudioContext;
    if (AudioContext) {
        this.audioCtx = new AudioContext();
        this.audioCtx.resume(); // 오디오 깨우기
    }

    const fileInput = document.createElement('input');
    fileInput.type = 'file';
    fileInput.accept = '.txt';
    fileInput.onchange = (e) => {
      // @ts-ignore
      const file = e.target.files[0];
      if (file) this.loadReplayFile(file);
    };
    fileInput.click();
  }

  loadReplayFile(file) {
    this.updateStatus('리플레이 파일 로딩 중...');
    const recordBtn = document.getElementById('record-btn');
    // @ts-ignore
    if (recordBtn) recordBtn.disabled = true;
    const loadingOverlay = document.getElementById('loading-overlay');
    // @ts-ignore
    if (loadingOverlay) loadingOverlay.classList.remove('hidden');

    const reader = new FileReader();
    reader.onload = (event) => {
      try {
        // @ts-ignore
        const packWithComment = JSON.parse(event.target.result);
        const pack = packWithComment.pack;
        
        // [수정: Hash 검증 로직 수정]
        const originalHash = pack.hash; // 원본 해시 저장
        pack.hash = 0; // 해시 계산을 위해 0으로 초기화
        if (originalHash !== getHashCode(serialize(pack))) {
             throw new Error('Hash mismatch');
        }
        // 검증 후 원래대로 복구 (선택사항이나 안전을 위해)
        pack.hash = originalHash; 

        this.initializeRenderer(pack);
      } catch (err) {
        console.error(err);
        this.updateStatus('파일 로딩 실패: 해시 불일치 또는 손상된 파일');
        // @ts-ignore
        if (recordBtn) recordBtn.disabled = false;
        // @ts-ignore
        if (loadingOverlay) loadingOverlay.classList.add('hidden');
      }
    };
    reader.readAsText(file);
  }

  initializeRenderer(pack) {
    this.updateStatus('에셋 로딩 중...');
    this.currentPack = pack;

    this.renderer = autoDetectRenderer({
      width: 432,
      height: 304,
      antialias: false,
      backgroundColor: 0x000000,
      backgroundAlpha: 1,
      forceCanvas: true, // [중요] 2D Context 접근을 위해 필수
      preserveDrawingBuffer: true,
    });

    this.stage = new Container();
    this.ticker = new Ticker();
    this.ticker.minFPS = 1;
    this.loader = new Loader();

    const container = document.querySelector('#game-canvas-container');
    // @ts-ignore
    container.innerHTML = '';
    // @ts-ignore
    container.appendChild(this.renderer.view);

    this.loader.add(ASSETS_PATH.SPRITE_SHEET);
    for (const prop in ASSETS_PATH.SOUNDS) {
      this.loader.add(ASSETS_PATH.SOUNDS[prop]);
    }

    this.setupLoaderProgressBar();
    this.loader.load(() => this.setupGame(pack));
  }

  setupLoaderProgressBar() {
    const progressBar = document.getElementById('progress-bar');
    if (progressBar) {
      this.loader.onProgress.add(() => {
        // @ts-ignore
        progressBar.style.width = `${this.loader.progress}%`;
      });
    }
  }

  setupGame(pack) {
    this.updateStatus('게임 초기화 중...');

    // DOM 요소 자동 생성 (HTML에 없어도 동작하게 함)
    this.ensureDOMElements();

    this.pikaVolley = new PikachuVolleyballReplay(
      this.stage,
      this.loader.resources,
      pack.roomID,
      pack.nicknames,
      pack.partialPublicIPs,
      pack.inputs,
      pack.options,
      pack.chats
    );

    try {
      // @ts-ignore
      setGetSpeechBubbleNeeded(this.pikaVolley);
    } catch (e) {}

    // 채팅 표시 활성화 (입력 기능인 enableChat은 호출하지 않음)
    this.pikaVolley.willDisplayChat = true;

    // 오디오 설정
    this.setupAudio();

    // 렌더링 루프 설정
    this.ticker.add(() => {
      this.renderer.render(this.stage);
      
      // [중요] 에러가 나도 다음 프레임으로 강제 진행 (멈춤 방지)
      try {
          if (this.pikaVolley) this.pikaVolley.gameLoop();
      } catch (err) {
          if (this.pikaVolley) this.pikaVolley.replayFrameCounter++;
      }

      // [2] 캔버스 오버레이 그리기 (닉네임/채팅을 영상에 포함)
      this.drawOverlaysOnCanvas();
    });

    setTimeout(() => this.startRecording(), 500);
  }

  ensureDOMElements() {
    const container = document.getElementById('game-canvas-container');
    if (!container) return;
    
    // 필요한 DOM 요소가 없으면 생성 (게임 로직이 에러나지 않게 하기 위함)
    const ids = ['player1-chat-box', 'player2-chat-box', 'player1-nickname', 'player2-nickname', 'player1-partial-ip', 'player2-partial-ip'];
    ids.forEach(id => {
        let el = document.getElementById(id);
        if (!el) {
            el = document.createElement('div');
            el.id = id;
            container.appendChild(el);
        }
        // 화면엔 안 보여도 됨 (캔버스에 그릴 거니까, 위치만 잡히게 display:block 대신 opacity:0 등 활용 가능하나 여기선 display:none도 텍스트 읽는데 지장없음)
        el.style.display = 'none'; 
        el.textContent = ''; // 초기화
    });
  }

  setupAudio() {
    // BGM 끄기, 효과음 켜기
    this.pikaVolley.audio.turnBGMVolume(false);
    this.pikaVolley.audio.turnSFXVolume(true);
    
    // BGM 완전 차단
    if (this.pikaVolley.audio.sounds.bgm?.center) {
        this.pikaVolley.audio.sounds.bgm.center.stop();
        // @ts-ignore
        this.pikaVolley.audio.sounds.bgm.center.play = () => {};
    }

    // [3] 효과음 캡처 준비 (Web Audio API 연결)
    try {
        // PIXI Sound가 사용하는 컨텍스트 가져오기 시도
        // @ts-ignore
        const pixiSound = window.PIXI && window.PIXI.sound;
        const ctx = pixiSound && pixiSound.context ? pixiSound.context.audioContext : this.audioCtx;
        
        if (ctx) {
            this.audioCtx = ctx;
            this.audioDest = this.audioCtx.createMediaStreamDestination();
            
            // PIXI Sound의 마스터 노드를 녹음 목적지에 연결
            if (pixiSound && pixiSound.context && pixiSound.context.masterGain) {
                pixiSound.context.masterGain.connect(this.audioDest);
                console.log("🔊 Audio connected to recorder via PIXI masterGain");
            } else {
                console.warn("⚠️ PIXI sound masterGain not found, audio recording might fail");
            }
        }
    } catch (e) {
        console.warn('Audio capture setup warning:', e);
    }
  }

  // [핵심] HTML 텍스트를 읽어서 캔버스에 직접 그리기
  drawOverlaysOnCanvas() {
    // @ts-ignore
    const ctx = this.renderer.view.getContext('2d');
    if (!ctx) return;

    ctx.save();
    ctx.font = 'bold 16px "Segoe UI", sans-serif';
    ctx.textAlign = 'center';
    ctx.textBaseline = 'top';

    // 1. 닉네임 & IP 그리기 (HTML 요소의 텍스트를 가져옴)
    // 주의: ensureDOMElements에서 id를 생성했으므로 getElementById는 안전함
    const p1Nick = document.getElementById('player1-nickname')?.textContent || '';
    const p1IP = document.getElementById('player1-partial-ip')?.textContent || '';
    const p2Nick = document.getElementById('player2-nickname')?.textContent || '';
    const p2IP = document.getElementById('player2-partial-ip')?.textContent || '';

    // Player 1 (Left)
    if (p1Nick) {
        ctx.fillStyle = 'white';
        ctx.strokeStyle = 'black';
        ctx.lineWidth = 3;
        ctx.strokeText(p1Nick, 50, 10);
        ctx.fillText(p1Nick, 50, 10);
        if (p1IP) {
            ctx.font = '12px sans-serif';
            ctx.strokeText(p1IP, 50, 30);
            ctx.fillText(p1IP, 50, 30);
        }
    }

    // Player 2 (Right)
    if (p2Nick) {
        ctx.font = 'bold 16px "Segoe UI", sans-serif';
        // 캔버스 너비 432 기준
        ctx.strokeText(p2Nick, 432 - 50, 10);
        ctx.fillText(p2Nick, 432 - 50, 10);
        if (p2IP) {
            ctx.font = '12px sans-serif';
            ctx.strokeText(p2IP, 432 - 50, 30);
            ctx.fillText(p2IP, 432 - 50, 30);
        }
    }

    // 2. 채팅 말풍선 그리기
    this.drawChatBubble(ctx, 'player1-chat-box', 80, 60);
    this.drawChatBubble(ctx, 'player2-chat-box', 432 - 80, 60);

    ctx.restore();
  }

  drawChatBubble(ctx, elementId, x, y) {
      const el = document.getElementById(elementId);
      if (!el || !el.textContent || el.textContent.trim() === '') return;

      const text = el.textContent;
      ctx.font = '14px sans-serif';
      const textMetrics = ctx.measureText(text);
      const width = textMetrics.width + 20; // 패딩 추가
      const height = 30;

      // 말풍선 배경
      ctx.fillStyle = 'rgba(255, 255, 255, 0.9)';
      ctx.fillRect(x - width / 2, y, width, height);
      ctx.strokeStyle = 'black';
      ctx.lineWidth = 1;
      ctx.strokeRect(x - width / 2, y, width, height);

      // 텍스트
      ctx.fillStyle = 'black';
      ctx.fillText(text, x, y + 6);
  }

  startRecording() {
    this.updateStatus('녹화 시작...');
    this.recordedChunks = [];
    this.isRecording = true;

    // @ts-ignore
    const canvas = this.renderer.view;
    const canvasStream = canvas.captureStream(30); // 30 FPS

    // 오디오 트랙 합치기
    if (this.audioDest) {
        const audioTrack = this.audioDest.stream.getAudioTracks()[0];
        if (audioTrack) {
            canvasStream.addTrack(audioTrack);
            console.log('🔊 Audio track added to video stream');
        }
    }

    try {
      this.mediaRecorder = new MediaRecorder(canvasStream, {
        mimeType: 'video/webm;codecs=vp9',
        videoBitsPerSecond: 2500000
      });
    } catch (e) {
      console.warn("VP9 not supported, falling back to default");
      this.mediaRecorder = new MediaRecorder(canvasStream); // Fallback
    }

    this.mediaRecorder.ondataavailable = (e) => {
      if (e.data.size > 0) this.recordedChunks.push(e.data);
    };

    this.mediaRecorder.onstop = () => this.saveVideoFile();
    
    this.mediaRecorder.start(100);
    
    this.ticker.maxFPS = 30; // 녹화 속도 동기화
    this.ticker.start();
    
    this.monitorProgress();
  }

  monitorProgress() {
    const totalFrames = this.pikaVolley.inputs.length;
    const interval = setInterval(() => {
        if (!this.isRecording) { clearInterval(interval); return; }
        
        const current = this.pikaVolley.replayFrameCounter;
        const percent = Math.min(100, Math.floor((current / totalFrames) * 100));
        this.updateStatus(`녹화 중... ${percent}%`);
        
        const progressBar = document.getElementById('progress-bar');
        // @ts-ignore
        if (progressBar) progressBar.style.width = `${percent}%`;

        if (current >= totalFrames) {
            clearInterval(interval);
            this.stopRecording();
        }
    }, 500);
  }

  stopRecording() {
    if (!this.isRecording) return;
    this.updateStatus('저장 중...');
    this.ticker.stop();
    this.mediaRecorder.stop();
    this.isRecording = false;
  }

  saveVideoFile() {
    if (this.recordedChunks.length === 0) return;
    
    const blob = new Blob(this.recordedChunks, { type: 'video/webm' });
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = `pikavolley_replay_${Date.now()}.webm`;
    a.style.display = 'none';
    document.body.appendChild(a);
    a.click();
    
    setTimeout(() => {
        document.body.removeChild(a);
        URL.revokeObjectURL(url);
    }, 100);

    this.updateStatus('완료!');
    const recordBtn = document.getElementById('record-btn');
    // @ts-ignore
    if (recordBtn) recordBtn.disabled = false;
    const loadingOverlay = document.getElementById('loading-overlay');
    // @ts-ignore
    if (loadingOverlay) loadingOverlay.classList.add('hidden');
  }
}

// 경로 수정
function adjustAssetsPath() {
  const prefix = '../../';
  if (!ASSETS_PATH.SPRITE_SHEET.startsWith(prefix)) {
    const clean = (p) => p.replace(/^\.\.\//, '');
    ASSETS_PATH.SPRITE_SHEET = prefix + clean(ASSETS_PATH.SPRITE_SHEET);
    for (const k in ASSETS_PATH.SOUNDS) ASSETS_PATH.SOUNDS[k] = prefix + clean(ASSETS_PATH.SOUNDS[k]);
  }
}
adjustAssetsPath();

const converter = new ReplayToVideoConverter();
const recordBtn = document.getElementById('record-btn');
if (recordBtn) {
  recordBtn.addEventListener('click', () => converter.handleRecordButtonClick());
}